@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
    button.SortBtn {
        @apply p-2.5 bg-background h-[40px] w-[40px] rounded-md border-2 border-transparent hover:border-pink-light;
    }

    button.SortBtn svg {
        @apply fill-text-light hover:fill-pink;
    }

    button.SortBtn-active {
        @apply bg-pink;
    }
    button.SortBtn-active svg {
        @apply fill-white hover:fill-white;
    }
}